<script>
import { ref, onMounted, nextTick } from "vue";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { Observer } from "gsap/Observer";

/* eslint-disable no-irregular-whitespace */
export default {
  setup() {
    const bannerImg = ref(require("@/assets/banner.jpg"));
    const iconImg = ref(require("@/assets/icon.png"));
    const artistImg = ref(require("@/assets/artist2.jpg"));
    const artworkImgs = ref([]);
    const logoColor = ref("");
    // const windowWidth = ref(window.innerWidth);

    //  assets/flat  assets/solid 
    const flatImages = require
      .context("@/assets/flat/normalized/", false, /\.(png|jpe?g|svg)$/)
      .keys()
      .map((key) =>
        require(`@/assets/flat/normalized/${key.replace("./", "")}`)
      );
    const solidImages = require
      .context("@/assets/solid/normalized/", false, /\.(png|jpe?g|svg)$/)
      .keys()
      .map((key) =>
        require(`@/assets/solid/normalized/${key.replace("./", "")}`)
      );

    // 
    artworkImgs.value = [...flatImages, ...solidImages].sort(
      () => Math.random() - 0.5
    );

    onMounted(async () => {
      gsap.registerPlugin(ScrollTrigger, Observer);

      await nextTick();

      //load element && banner anition
      const imgElement = document.querySelector(".img");
      if (imgElement) {
        imgElement.onload = () => {
          const imgWidth = imgElement.offsetWidth - 1;
          gsap.to(".banner-animation .imgs", {
            x: -imgWidth * 2,
            duration: 200,
            ease: "linear",
            repeat: -1,
          });
        };
      } else {
        console.error("imgElement not found");
      }

      //banner animation
      gsap.from(".banner-container .bottom img", {
        opacity: 0,
        duration: 5,
        ease: "power2.out",
      });

      //first section animation
      var firstAnimation = gsap.timeline({
        repeat: -1,
        repeatDelay: 0,
        yoyo: true,
      });
      firstAnimation.fromTo(
        ".first",
        {
          clipPath: "polygon(0 0, 100% 10%, 100% 95%, 0 100%)",
        },
        {
          clipPath: "polygon(0 3%, 100% 10%, 100% 91%, 0 98%)",
          duration: 4,
          ease: "linear",
        }
      );
      firstAnimation.fromTo(
        ".first",
        {
          clipPath: "polygon(0 3%, 100% 10%, 100% 91%, 0 98%)",
        },
        {
          clipPath: "polygon(0 2%, 100% 7%, 100% 94%, 0 98%)",
          duration: 4,
          ease: "linear",
        }
      );

      //second section animation
      var secondAnimation = gsap.timeline({
        repeat: -1,
        repeatDelay: 0,
        yoyo: true,
      });
      secondAnimation.fromTo(
        [".first-img", ".first-img"],
        {
          backgroundPositionX: "69%",
        },
        {
          backgroundPositionX: "59%",
          duration: 30,
          ease: "linear",
          yoyo: true,
          repeat: -1,
        }
      );
      secondAnimation.fromTo(
        [".second-img", ".second-img"],
        {
          backgroundPositionX: "5%",
        },
        {
          backgroundPositionX: "30%",
          duration: 10,
          ease: "linear",
          yoyo: true,
          repeat: -1,
        },
        "<"
      );
      secondAnimation.fromTo(
        [".third-img", ".third-img"],
        {
          backgroundPositionX: "20%",
        },
        {
          backgroundPositionX: "50%",
          duration: 15,
          ease: "linear",
          yoyo: true,
          repeat: -1,
        },
        "<"
      );
      var secondTitleAnimation = gsap.timeline({
        scrollTrigger: {
          trigger: ".second .title",
          start: "top 40%",
          end: "bottom 40%",
        },
      });
      secondTitleAnimation.from(".second .title .first-title", {
        x: -100,
        opacity: 0,
        duration: 1,
        ease: "power2.out",
      });
      secondTitleAnimation.from(
        ".second .title .second-title",
        {
          x: -100,
          opacity: 0,
          duration: 1,
          delay: 0.3,
          ease: "power2.out",
        },
        "<"
      );
      secondTitleAnimation.from(".second .container .img", {
        opacity: 0,
        duration: 1,
        stagger: 0.5,
      });

      //third section animation
      var thirdAnimation = gsap.timeline({
        repeat: -1,
        repeatDelay: 0,
        yoyo: true,
      });
      thirdAnimation.fromTo(
        ".third",
        {
          clipPath: "polygon(0 10%, 100% 0, 100% 100%, 0 95%)",
        },
        {
          clipPath: "polygon(0 10%, 100% 3%, 100% 98%, 0 91%)",
          duration: 4,
          ease: "linear",
        }
      );
      thirdAnimation.fromTo(
        ".third",
        {
          clipPath: "polygon(0 10%, 100% 3%, 100% 98%, 0 91%)",
        },
        {
          clipPath: "polygon(0 7%, 100% 2%, 100% 98%, 0 94%)",
          duration: 4,
          ease: "linear",
        }
      );
      var thirdTextAnimation = gsap.timeline({
        scrollTrigger: {
          trigger: ".third .top",
          start: "top 40%",
          end: "bottom 40%",
        },
      });
      thirdTextAnimation.from(".third .top .text-content", {
        opacity: 0,
        duration: 1,
        ease: "power2.out",
      });
      thirdTextAnimation.from(
        ".third .bottom .img",
        {
          opacity: 0,
          duration: 1,
          ease: "power2.out",
          delay: 0.3,
        },
        "<"
      );

      //fourth section animation
      var fourthAnimation = gsap.timeline({
        repeat: -1,
        repeatDelay: 0,
        yoyo: true,
      });
      fourthAnimation.fromTo(
        [".fourth .container .imgs", ".fourth .container .imgs"],
        { yPercent: 0 },
        { yPercent: -95, duration: 400, ease: "linear", repeat: -1 },
        "<"
      );
      var fourthTitleAnimation = gsap.timeline({
        scrollTrigger: {
          trigger: ".fourth .title",
          start: "top 40%",
          end: "bottom 40%",
        },
      });
      fourthTitleAnimation.from(".fourth .title .first-title", {
        x: -100,
        opacity: 0,
        duration: 1,
        ease: "power2.out",
      });
      fourthTitleAnimation.from(
        ".fourth .title .second-title",
        {
          x: -100,
          opacity: 0,
          duration: 1,
          delay: 0.3,
          ease: "power2.out",
        },
        "<"
      );
      fourthTitleAnimation.from(
        ".fourth .container .imgs",
        {
          opacity: 0,
          duration: 1,
        },
        "<"
      );

      //footer animation
      var footerAnimation = gsap.timeline({
        repeat: -1,
        repeatDelay: 0,
        yoyo: true,
      });
      footerAnimation.fromTo(
        ["footer", "footer"],
        { clipPath: "polygon(0 0, 100% 10%, 100% 100%, 0 100%)" },
        {
          clipPath: "polygon(0 5%, 100% 25%, 100% 100%, 0 100%)",
          duration: 4,
          ease: "linear",
        }
      );
      footerAnimation.fromTo(
        ["footer", "footer"],
        { clipPath: "polygon(0 5%, 100% 25%, 100% 100%, 0 100%)" },
        {
          clipPath: "polygon(0 2%, 100% 15%, 100% 100%, 0 100%)",
          duration: 4,
          ease: "linear",
        }
      );

      //hide header on scroll down
      var scrollDown = () => {
        gsap.to("header", {
          yPercent: -100,
          duration: 1.5,
          ease: "power2.out",
        });
      };

      //show header on scroll up
      var scrollUp = () => {
        gsap.to("header", {
          yPercent: 0,
          duration: 1.5,
          ease: "power2.out",
        });
      };

      Observer.create({
        type: "wheel,touch,pointer",
        wheelSpeed: -1,
        onDown: () => scrollUp(),
        onUp: () => scrollDown(),
      });
    });

    return {
      bannerImg,
      iconImg,
      artistImg,
      artworkImgs,
      logoColor,
    };
  },
};
</script>

<template>
  <div class="loader">
    <div class="spinner">
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
    </div>
  </div>

  <header>
    <h1>Seaport Handicrafts</h1>
    <h6>- Contemporary Knot Art of Hsu Pei-Tzu -</h6>
  </header>

  <div class="banner-animation">
    <div class="imgs">
      <img v-for="i in 5" :key="i" :src="bannerImg" class="img" />
    </div>
  </div>
  <section class="banner-container">
    <div class="bottom">
      <img :src="iconImg" />
    </div>
  </section>

  <main>
    <section class="first">
      <div class="text-content">
        <p>
          <br /><br />
          <br /><br />
          ()124
        </p>
      </div>
    </section>

    <section class="second">
      <div class="title">
        <h1 class="first-title">Seaport</h1>
        <h1 class="second-title">Exhibition</h1>
      </div>
      <div class="container">
        <div class="top">
          <div class="img first-img"></div>
          <div class="img second-img"></div>
        </div>
        <div class="bottom">
          <div class="img third-img"></div>
        </div>
      </div>
    </section>

    <section class="third">
      <div class="top">
        <div class="text-content">
          <h1><span>Hsu Pei-Tzu</span></h1>
          <p>
            <br />
            (/)
          </p>
        </div>
      </div>
      <div class="bottom">
        <div
          class="img"
          :style="{ 'background-image': `url(${artistImg})` }"
        ></div>
      </div>
    </section>
  </main>

  <section class="fourth">
    <div class="top">
      <div class="title">
        <h1 class="first-title">Contemporary</h1>
        <h1 class="second-title">Knot Art</h1>
      </div>
    </div>
    <div class="bottom">
      <div class="container">
        <div class="imgs">
          <img v-for="img in artworkImgs" :key="img" :src="img" />
        </div>
      </div>
    </div>
  </section>

  <footer>
    <div class="text-content">
      <p>
        Copyright  2024 Hsu Pei-Tzu. Designed and maintained by Kamaboko Wu.
        All rights reserved.
      </p>
    </div>
  </footer>
</template>

<style scoped lang="scss">
.loader {
  position: fixed;
  z-index: 99999;
  height: 100vh;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  display: none;
}
.spinner {
  --radius: 100;
  --size: 50;
  position: relative;
  width: calc(var(--size) * 1px);
  height: calc(var(--size) * 1px);
  div {
    position: absolute;
    width: 100%;
    height: 100%;
    background: #474bff;
    border-radius: 50%;
    transform: rotate(calc(var(--angle) * 1deg))
      translate(0, calc(var(--radius) * 0px));
    animation: spinner-19rk4d 1.5s calc(var(--delay) * 1.2s) infinite ease;

    @keyframes spinner-19rk4d {
      0%,
      30%,
      50%,
      100% {
        transform: rotate(calc(var(--angle) * 1deg))
          translate(0, calc(var(--radius) * 0px)) scale(0);
      }

      40% {
        transform: rotate(calc(var(--angle) * 1deg))
          translate(0, calc(var(--radius) * 1px)) scale(1);
      }
    }
    &:nth-child(1) {
      --angle: 45;
      --delay: 0.1;
    }
    &:nth-child(2) {
      --angle: 90;
      --delay: 0.2;
    }
    &:nth-child(3) {
      --angle: 135;
      --delay: 0.3;
    }

    &:nth-child(4) {
      --angle: 180;
      --delay: 0.4;
    }

    &:nth-child(5) {
      --angle: 225;
      --delay: 0.5;
    }

    &:nth-child(6) {
      --angle: 270;
      --delay: 0.6;
    }

    &:nth-child(7) {
      --angle: 315;
      --delay: 0.7;
    }

    &:nth-child(8) {
      --angle: 360;
      --delay: 0.8;
    }
  }
}

header {
  height: 150px;
  width: 100%;
  display: flex;
  align-items: flex-start;
